import React from 'react';
import { VelocityTransitionGroup } from 'velocity-react';

import Item from './item.jsx';

export default class List extends React.Component {

	render() {
		const { comments } = this.props;

		return (
			<ul className=" max-width-9 list-margin-bottom-equaliser">
				<VelocityTransitionGroup runOnMount={false} component='li' enter={{ animation: 'slideDown', easing: 'easeInOutQuad', duration: 400 }} leave={{ animation: 'slideUp', easing: 'easeInOutQuad', duration: 400 }}>
					{comments.map(comment => {
						return (
							<div key={comment.id} className="margin-bottom">
								<Item comment={comment} />
								{comment.children && comment.children.length ? <div className="margin-left margin-top"><List comments={comment.children} /></div> : null}
							</div>
						);
					})}
				</VelocityTransitionGroup>
			</ul>
		);
	}

}