import React from 'react';
import { Formik } from 'formik';

export default class Form extends React.Component {

	render() {
		const { title, comment, onCancel } = this.props;

		return (
			<div className="background-color-white border border-color-grey border-radius overflow-hidden box-shadow font-family-raleway padding-bottom padding-horizontal padding-top-medium">
				<small className="color-muted-grey inline-block font-weight-normal margin-bottom-medium margin-top-small">{title}</small>
				<Formik initialValues={comment ? comment : {}} validate={this.validate} onSubmit={this.onSubmit}>
					{({
						values,
						errors,
						touched,
						handleChange,
						handleBlur,
						handleSubmit,
						isSubmitting,
					}) => (
						<form onSubmit={handleSubmit}>
							<textarea className="form-field line-height-copy font-size-medium max-width-100 min-width-100" name="text" onChange={handleChange} onBlur={handleBlur} value={values.text} />
							<button type="submit" className="button button--small button--outline color-muted-grey margin-top-medium">Submit</button>
							{onCancel ? <button onClick={onCancel} type="button" className="button button--small button--outline color-muted-grey margin-top-medium margin-left-medium">Cancel</button> : null}
						</form>
					)}
				</Formik>
			</div>
		);
	}

}