import React, { createRef } from 'react';
import Velocity from 'velocity-animate';

import './styles.scss';

export default class Introduction extends React.Component {

	constructor(props) {
		super(props);

		this.f = createRef();
		this.u = createRef();
		this.s = createRef();
		this.r = createRef();
		this.n = createRef();
		this.wrapper = createRef();
		this.title = createRef();
		this.space = createRef();
		this.button = createRef();
		this.intro = createRef();
		this.myNameIs = createRef();
	}

	animate = event => {
		const { push } = this.props.actions;
		event && event.preventDefault();

		Velocity(this.button.current, { opacity: 0 });
		Velocity(this.button.current, { display: 'none' });

		setTimeout(() => {
			Velocity(this.intro.current, { opacity: 1 }, { duration: 600 });

			setTimeout(() => {
				Velocity(this.myNameIs.current, { opacity: 1 }, { duration: 600 });

				setTimeout(() => {
					Velocity(this.f.current, { color: '#000' });

					setTimeout(() => {
						Velocity(this.s.current, { color: '#000' });

						setTimeout(() => {
							Velocity(this.r.current, { color: '#000' });

							setTimeout(() => {
								Velocity(this.n.current, { color: '#000' });

								setTimeout(() => {
									Velocity(this.f.current, { opacity: 0 });
									Velocity(this.s.current, { opacity: 0 });
									Velocity(this.r.current, { opacity: 0 });
									Velocity(this.n.current, { opacity: 0 });

									setTimeout(() => {
										Velocity(this.f.current, { width: '0px' });
										Velocity(this.u.current, { width: '0px' });
										Velocity(this.s.current, { width: '0px' });
										Velocity(this.space.current, { width: '0px' });
										Velocity(this.r.current, { width: '0px' });
										Velocity(this.n.current, { width: '0px' });

										setTimeout(() => {
											Velocity(this.intro.current, { opacity: 0 });

											setTimeout(() => {
												Velocity(this.title.current, { opacity: 0 });
												Velocity(this.wrapper.current, { opacity: 0 }, { duration: 1200 });

												setTimeout(() => {
													push('/comments');
												}, 600);
											}, 1200);
										}, 1000);
									}, 400);
								}, 400);
							}, 400);
						}, 400);
					}, 400);
				}, 1000);
			}, 1000);
		}, 1000);
	};

	render() {
		return (
			<div ref={this.wrapper} className='page full-height background-color-dark-blue justify-centre color-white'>
				<div className='text-align-center font-family-comfortaa'>
					<p ref={this.intro} className='margin-bottom transparent'>Hi, <span className='transparent' ref={this.myNameIs}>my name is</span></p>
					<h1 ref={this.title} className='site-title margin-bottom-medium'><span ref={this.f}>f</span><span>erg</span><span ref={this.u}>u</span><span ref={this.s}>s</span><span className='transparent' ref={this.space}>&nbsp;</span><span ref={this.r}>r</span><span>usto</span><span ref={this.n}>n</span></h1>
					<div className="introduction-start-container text-align-center">
						<button ref={this.button} onClick={this.animate} className='introduction-start-button button button--fade button--ghost'>Start</button>
					</div>
				</div>
			</div>
		);
	}

}