import { createReducer } from 'lib';
import getInitialState from './initialState.js';

import {
	COMMENT_ADD_REQUEST,
	COMMENT_ADD_SUCCESS,
	COMMENT_ADD_FAILURE,

	COMMENT_EDIT_REQUEST,
	COMMENT_EDIT_SUCCESS,
	COMMENT_EDIT_FAILURE,

	COMMENT_DELETE_REQUEST,
	COMMENT_DELETE_SUCCESS,
	COMMENT_DELETE_FAILURE
} from './actionTypes.js';

export default createReducer(getInitialState(), {
	[COMMENT_ADD_REQUEST]: (state, payload) => {
		return Object.assign({}, state, {
			loading: true,
			error: null
		});
	},
	[COMMENT_ADD_SUCCESS]: (state, payload) => {
		return Object.assign({}, state, {
			items: [].concat([payload.item], state.items),
			loading: false,
			error: false
		});
	},
	[COMMENT_ADD_FAILURE]: (state, payload) => {

	},
	[COMMENT_EDIT_SUCCESS]: (state, payload) => {
		const { item } = payload;

		return Object.assign({}, state, {
			items: state.items.map(object => item.id === object.id ? item : object)
		});
	},
	[COMMENT_EDIT_FAILURE]: (state, payload) => {

	},
	
	[COMMENT_DELETE_REQUEST]: (state, payload) => {
		return Object.assign({}, state, {
			loading: true
		});
	},
	[COMMENT_DELETE_SUCCESS]: (state, payload) => {

	},
	[COMMENT_DELETE_FAILURE]: (state, payload) => {

	},

});