import React from 'react';
import ReactMarkdown from 'react-markdown';
import { VelocityTransitionGroup } from 'velocity-react';

import ReplyForm from '../containers/reply.jsx';
import EditForm from '../containers/edit.jsx';

export default class Item extends React.Component {

	state = {
		showEdit: false,
		showReply: false
	};

	hideEditForm = () => {
		this.setState({
			showEdit: false
		});
	};

	hideReplyForm = () => {
		this.setState({
			showReply: false
		});
	};

	onReplyClick = event => {
		const { showReply } = this.state;

		this.setState({
			showEdit: false,
			showReply: !showReply
		});
	};

	onEditClick = event => {
		const { showEdit } = this.state;

		this.setState({
			showReply: false,
			showEdit: !showEdit
		});
	};

	onRemoveClick = event => {
		const { onRemoveClick } = this.props;

		if (onRemoveClick) onRemoveClick();
	};

	renderEditForm() {
		const { comment } = this.props;

		return (
			<div className="padding-top">
				<EditForm onCancel={this.hideEditForm} comment={comment} />
			</div>
		);
	}

	renderReplyForm() {
		return (
			<div className="padding-top">
				<ReplyForm onCancel={this.hideReplyForm} />
			</div>
		);
	}

	render() {
		const { comment } = this.props;
		const { showEdit, showReply } = this.state;

		return (
			<div>
				<div className="background-color-white border border-color-grey border-radius overflow-hidden box-shadow font-family-raleway">
					<div className="padding-horizontal-large padding-vertical">
						<small className="color-muted-grey margin-bottom-medium block">{comment.username}</small>
						<div className="line-height-copy font-size-medium color-black">
							<ReactMarkdown source={comment.text} />
						</div>
					</div>
					<footer className="box-shadow-inset border-top border-color-grey border-bottom-radius background-color-light-grey padding-vertical-small font-size-tiny padding-horizontal-large">
						<button onClick={this.onReplyClick} href="#" className="color-muted-grey color-black--on-hover cursor-pointer no-decoration margin-right">reply</button>
						<button onClick={this.onEditClick} href="#" className="color-muted-grey color-black--on-hover cursor-pointer no-decoration margin-right">edit</button>
						<button onClick={this.onRemoveClick} href="#" className="color-muted-grey color-black--on-hover cursor-pointer no-decoration">remove</button>
					</footer>
				</div>
				<VelocityTransitionGroup component='div' enter={{ animation: 'slideDown', duration: 300 }} leave={{ animation: 'slideUp', duration: 300 }}>
					{showEdit ? this.renderEditForm() : null}
					{showReply ? this.renderReplyForm() : null}
				</VelocityTransitionGroup>
			</div>
		);
	}

}