import { connect } from 'react-redux';
import { push } from 'connected-react-router';

import Component from '../components/list.jsx';

const mapStateToProps = (state) => {
	const { comments: { items } } = state;
	if(items) {
		const getComment = comment => {
			const children = items.filter(child => child.parentId === comment.id);
			comment.children = children.map(getComment);
			return comment;
		}

		const models = items.filter(comment => !comment.parentId).map(getComment);

		return {
			comments: models
		};
	} else {
		return state;
	}
};

const mapDispatchToProps = dispatch => {
	return {
		actions: {
			push
		}
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Component);